export default {"name":"狮口银芽","img":"\\lvcha\\shikouyinya\\img_20191122100056dop7d8.jpg","desc":["狮口银牙产于产于湘西土家族苗族自治州古丈县古阳河畔的狮子口茶场茶叶专业户。全身白毫，闪光隐翠紧结圆直，锋苗秀丽，清香馥郁，滋味甘爽。"],"menus":[{"name":"狮口银芽茶叶特点","content":[{"text":"狮口银芽\n        成茶品质特点是，全身白毫，具有银毫闪光、隐翠，外形紧结圆直，锋苗秀丽，内质清香馥郁，汤色黄绿明亮，滋味甘爽，叶底嫩绿。"},{"src":"\\lvcha\\shikouyinya\\img_20191122100056jkw9yn.jpg"}]},{"name":"狮口银芽加工工艺","content":[{"text":"一、杀青"},{"text":"杀青即通过高温破坏鲜叶的组织，使鲜叶内含物迅速转化。杀青是\n        绿茶\n        初制的第一道工序，是达到绿茶清汤绿叶品质的关键。鲜叶通过高温杀青，可以达到："},{"src":"\\lvcha\\shikouyinya\\img_201911221000565volt9.jpg"},{"text":"1、破坏鲜叶中酶活性，制止多酚类化合物的酶性氧化，防止叶子红变，为保持绿茶绿叶清汤的品质特征奠定基础。"},{"text":"2、蒸发叶内一部分水分，增强叶片韧性，为揉捻成条创造条件。"},{"text":"3、是叶内具有青臭气的低沸点芳香物质挥发，高沸点芳香物质显露，增进茶香。"},{"text":"杀青的技术因素包括锅温、投叶量、时间和方法等。这因素相互制约、相互促进、共同影响杀青的质量。"},{"text":"(1)锅温一般要求杀青锅温有260℃～320℃之间，才能达到钝化酶活性的目的。锅温太高，杀青叶失水过快，易产生焦叶焦边，产生烟焦味。锅温太低，易产生红梗红叶，影响成品的品质。"},{"text":"(2)投叶量因杀青设备、杀青老嫩、锅温不同而异。在同一锅温条件下，采用机械传动的锅式杀青，其投叶量经验公式为："},{"text":"投叶量=(150-5K)·R3千克/锅，式中，K为鲜叶老嫩级别，R为杀青锅半径。"},{"text":"(3)杀青时间一般锅式杀青时间有5—10分钟之间。时间长短与锅温和投叶量有关。杀青时间过长，杀青叶失水过多，不利于做形;杀青时间过短，鲜叶的茶多酚、蛋白质等成分水解转化不充分，成品青涩味重。一般宜掌握“嫩叶老杀，老叶嫩杀”的原则。"},{"text":"(4)方法锅式杀青应掌握“抖闷结合，多抖少闷”的原则。抖杀就是将叶子扬高，以有利于水分的散失，青草气挥发，使清香透发，防止叶色黄变。闷杀是加盖不扬叶，使热蒸气有叶内作短时间的停留，0—50时最强烈，如温度继续升高，酶的活性开始钝化，当叶温升到70，酶的活性便遭到破坏。因此，在杀青前期若能使叶温迅速升高到70经上，便能有效制止红梗红叶。但如闷得时间，芽叶易黄熟并伴有水闷气，同样不符合茶叶品质要求。"},{"text":"鲜叶在锅内转动，待到由鲜绿转为翠绿，叶面失光泽，手握成团，稍有弹性，叶质较柔软，折梗不断，闻其香带有清香感，则要起锅，进行摊晾，转入揉捻。"},{"text":"目前，生产上除锅式杀青外，推广较多的是滚筒式杀青机。滚筒青机具有操作方便、劳动强度小、工效高、节省燃料、连续作业等优点，但由于在筒内滞留的时间过短，易生青涩味，同时由于在筒内水蒸气散发不畅，极易在筒内和筒口粘结叶子而生成烟焦味。"},{"text":"二、揉捻"},{"text":"揉捻是炒青绿茶成条的重要工序。揉捻是利用机械力使杀青叶在揉桶内受到推、压、扭、和摩擦等多种力的相互作用形成紧结的条索。揉捻还使叶片细胞组织破碎，促使部分多酚类物质氧化，减少炒青绿茶的苦涩味，增加浓醇味。除少数手工揉捻外都用机器揉捻。"},{"text":"机制绿茶的揉捻机各类很多，型号不一，性能各异。生产实践中制炒青绿茶不宜使用大桶揉机。大型机投叶量多，时间长，揉捻过程中叶温高，易产生黄熟现象。一般都选用桶径45厘米和55厘米的揉捻机，生产量大时也可采用65厘米揉捻机。应根据制茶种类和叶质嫩度确定投叶量与加压大小和揉捻时间。掌握“嫩叶轻揉，老叶重揉”、“轻—重—轻”和“抖揉结合”的原则进行操作。绿茶多为一次揉捻，嫩叶一般要揉20—25分钟，老叶采用重压长揉，解块分筛，分次揉捻，但总时间一般不超过50分钟。高档茶成条率在85%以上，细胞破碎率在45%以上;低档绿茶成条率在60%以上，细胞破碎率达65以上，即是揉捻完成的标志。"},{"text":"三、干燥"},{"text":"干燥是茶叶整形做形，固定茶叶品质，发展茶香的重要工序。由于所用的机器不一样，干燥的工艺可分为：全炒法、全滚法、滚炒法，由于工艺不同，生产的产品品质也有所不同。以滚炒法为例，干燥分为三部分即二青、三青、烘干。"},{"text":"二青二青的目的在于蒸发部分水分，减少粘性，提高芽叶的可塑性便于成形。揉捻叶的含水量为60%左右。可用滚筒杀青机，方法是，筒温70--80℃，投叶量15公斤，时间15分钟。二青叶适度标准为：减重率30%，含水量35%--40%;手捏茶叶有弹性，手握不易松散;叶质软，粘性，叶色绿，无烟焦和水闷气。"},{"text":"三青分2次进行，即初炒和复炒。初炒锅温70—100℃，投叶量13公斤左右，时间30—40分钟，此时含水量20%--25%。摊晾20—30分钟后，用5号筛进行筛分。筛面叶进行复炒。复炒叶和筛底茶分别滚至足干。复炒锅温为60—80℃，投叶量18公斤，时间40—60分钟，含水量在12%左右。"},{"text":"烘干温度掌握在50—60℃之间，采取先高后低。滚炒到含水量5%左右、手捏即成粉末时，出锅摊凉包装。"}]},{"name":"狮口银芽储藏方法","content":[{"text":"1、真空抽气充氮法"},{"text":"将茶叶装入铝箔与塑料做成的复合袋，采取一次性封闭真空抽气充氮包装贮存，也可适当加入些保鲜剂。一经启封后，最好在短时间内用完，否则时间一长，同样会陈化劣变。"},{"text":"2、冷库保鲜法"},{"text":"这种大量保鲜贮存法不加任何保鲜剂，纯属物理保鲜，只要库房建筑合理，库温保持在-4℃至2℃不变即可。但茶叶出库前必须要经过真空保鲜处理，否则，茶叶暴露空气中，由于外界冷热相遇，水分和氧气会形成水汽珠而凝结在茶叶上，加速茶叶劣变。"},{"src":"\\lvcha\\shikouyinya\\img_20191122100056m7amfw.jpg"},{"text":"3、瓶装法"},{"text":"常用的有热水瓶或专门装茶叶的玻璃瓶，隔绝空气中氧气的性能非常优越。将干茶装入瓶内，尽量装满整个瓶子，从而挤出瓶中所有的空气。用瓶塞将瓶口盖紧，用胶带、白蜡封口效果更好，可以最大限度的防止空气进入瓶内，防止茶叶被氧化。"},{"text":"4、袋装法"},{"text":"常见的有塑料袋和纸袋，这是目前家庭中使用最广泛、最简便、最便宜的储茶方法。但是在使用过程中需要注意，塑料袋必须用食品包装袋，以高密度、高强度、密封性强的塑料袋为佳，纸袋内层带有铝箔纸、锡箔纸的为好。还可以先把茶叶装入纸袋中封好口，再套上一层塑料袋。"}]},{"name":"狮口银芽冲泡方法","content":[{"text":"1、泡茶前烫杯。\n        狮口银芽\n        的泡制方法也别具一格，沏茶时，最好先倒半杯开水"},{"text":"2、茶与水的比例，大致掌握在1：50即150毫升的水，用3克左右的干茶。"},{"text":"3、加入适量的茶叶，一般根据口味加减，如喜喝浓茶加二分之一，喜喝淡茶加三分之一。"},{"text":"4、水温约80度时加水。从三泡台的侧壁加水，一不会烫坏茶叶二可使茶叶旋转灵动，利于出香。待水加满盖几秒钟，开盖搅动三下，倒入公道杯。"},{"text":"5、像上面程序再加水至满，盖几秒钟，搅动，倒入公道杯。此为第一道茶。第二道、第三道都依此方法进行。每一道茶需要依据经验掌握适当的时间和水温才能泡出茶的最好的味道。"}]},{"name":"狮口银芽功效作用","content":[{"text":"1、延缓衰老"},{"text":"茶叶里边含有一种名叫茶多酚的物质，具有很强的生理活性和抗氧化性，所以它可以有效的抗衰老，茶叶可以算得上是人体自由基天然的清除剂。"},{"text":"2、抑制心脑血管疾病"},{"text":"这是因为茶多酚可以促进体内的脂肪代谢，然而脂肪正是造成人体三高等主要原因，三高很容易造成心脑血管，所以经常饮用\n        绿茶\n        对于预防心脑血管非常有帮助。"},{"text":"3、消炎止泻"},{"text":"茶多酚有着非常明显的杀灭，抑制病毒的作用，在医学上人们已经将它运用在了治疗流感，痢疾等病症，效果非常好。"},{"text":"4、美容养颜"},{"text":"使用茶叶美容养颜，主要的方法是使用它来洗脸，这样可以起到控油和收缩毛孔的效果，可以防止皮肤老化，对于减少紫外线对皮肤的伤害也有很好的效果。"},{"text":"5、提神醒脑"},{"text":"茶叶里边含有一定的咖啡碱，咖啡碱可以增强大脑皮层的兴奋，促进神经中枢处于兴奋的状态，达到清心，提神的效果。"},{"text":"6、保护牙齿"},{"text":"茶叶属于碱性饮品，而且里边含有一定量的氟，可以减少人体对于钙质的吸收，所以它对于防止龋齿，保护牙齿都有很好的效果。"}]},{"name":"狮口银芽获奖情况","content":[{"text":"狮口银芽\n        一九八二年五月在南岳举行的全省名茶评选会上被评为全省名茶。六月选送参加了全国在长沙举行的名茶评选会，受到来自全国各地的茶叶专家一致好评，一九八三年、八四年均评为省、州、县优质产品。"},{"text":"一九八五年六月,由农枚渔业部，中国茶叶学会在江苏南京举行的全国名茶评选会上被评为全国16个邵级优质茶，并获得了优质证书和银杯奖。"},{"text":"一九八六年六月下旬，由省农业厅组织召开的全省名茶评选会上，又被评为全省十种仇质名茶之一,并获得了名茶证书。"}]}]}